.pickerWrapper {
  width: 200px; }

.datepicker-input {
  background-color: #FFF;
  border: none;
  height: 40px;
  padding: 10px; }

.calendarPresets {
  background: #FFF;
  position: absolute;
  left: -200px;
  top: 0;
  width: 200px; }
  .calendarPresets .presetItem {
    height: 20px; }

.flatpickr-months {
  background: #42a5f5; }
  .flatpickr-months .flatpickr-prev-month, .flatpickr-months .flatpickr-next-month {
    position: relative; }



