/**
  Base input styles
*/
.input {
  display: block;
  background: none;
  padding: .125rem .125rem .0625rem;
  font-size: 1rem;
  line-height: 1.9;
  width: 100%;
  -webkit-transition: all .28s ease;
  -o-transition: all .28s ease;
  transition: all .28s ease;
  -webkit-box-shadow: none;
          box-shadow: none;
  border: none;
  outline: none;
  border-bottom: 2px solid #D9D9D9;
  -webkit-appearance: textfield;
     -moz-appearance: textfield;
          appearance: textfield; }
  .input:focus ~ .label, .input:valid ~ .label {
    top: -20px;
    font-size: 12px;
    color: #60aac6; }
  .input:focus ~ .bar:before, .input:focus ~ .bar:after {
    width: 50%; }
  .input:focus:invalid ~ .hint {
    opacity: 1; }
  .input:valid ~ .hint {
    opacity: 0; }

.label {
  color: rgba(0, 0, 0, 0.3);
  position: absolute;
  top: .25rem;
  pointer-events: none;
  z-index: 1;
  font-size: .8rem;
  font-weight: normal;
  -webkit-transition: all 0.28s ease;
  -o-transition: all 0.28s ease;
  transition: all 0.28s ease; }
  .label + .group {
    margin: 30px 0 0; }

.group {
  position: relative;
  line-height: 24px;
  width: 100%;
  padding: 0 0 10px; }

.hint {
  position: absolute;
  opacity: 0;
  color: rgba(0, 0, 0, 0.3);
  -webkit-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
  -o-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
  transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
  top: .25rem;
  padding-left: .125rem;
  pointer-events: none;
  z-index: 1;
  font-size: 1rem;
  font-weight: normal; }

.bar {
  position: relative;
  display: block;
  width: 100%; }
  .bar:before, .bar:after {
    content: '';
    height: 2px;
    width: 0;
    bottom: 0;
    position: absolute;
    background: #60aac6;
    -webkit-transition: 0.2s ease all;
    -o-transition: 0.2s ease all;
    transition: 0.2s ease all; }
  .bar:before {
    left: 50%; }
  .bar:after {
    right: 50%; }

.errorText,
.successText {
  position: absolute;
  margin-top: 5px;
  left: 1px;
  font-size: 12px;
  line-height: 12px;
  color: #D14344;
  -webkit-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
  -o-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
  transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms; }

.successText {
  color: #70A60B; }

.invalid .input, .invalid .input:focus {
  border-color: #D14344; }

.invalid .input:focus ~ .label {
  color: #D14344; }

.invalid .bar:before, .invalid .bar:after {
  background: #D14344; }



